.form {
	width: 300px;
	margin-top: 50px;
}

.form__list {
	padding-left: 0;
}

.form__item {
	margin-top: 10px;
	display: flex;
	justify-content: space-between;
}

.form__fieldset {
	border: none;
	padding-left: 0;
}

.form__legend {
	font-size: 20px;
	font-style: bold;
}

.form__input {
	margin-left: 30px;
	border-color: #000000;
	border-radius: 4px; 
}

.form__button {
	width: 100px;
	height: 30px;
	background-color: #c0c0c0;
	margin-top: 20px;
	margin-bottom: 20px;
}

.form__button:hover {
	background-color: #a9a9a9;
}

.form__button:active {
	background-color: #808080;
	border: 0;
}
